@font-face {
  font-family: "Crewniverse";
  src: url("./assets/font/crewniverse_font-webfont.woff") format("woff");
  src: url("./assets/font/crewniverse_font-webfont.woff2") format("woff2"),
}

* {
  font-family: "Crewniverse";
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#card-area{
  height: 100%;
  width: 60%;
  display: flex;
  flex-flow: row wrap;
  padding: 0;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}